

.htc, .htc-r, .htc-l {
	padding: 5px;
	color: black;
	cursor: pointer;
	background-color: lightgray;
	padding-left:10px;
	padding-right:10px;
	text-decoration: none;
	border: 1px solid #aaa;
	margin-left: 0px;
	margin-right: -2px;
	margin-top:10px;
	display: inline-block;
	height: 25px;
}
.htc-l {
	border-radius:3px 0 0 3px;
}
.htc-r {
	border-radius:0 3px 3px 0;
	margin-right:5px;
}
.htc:hover, .htc-l:hover, .htc-r:hover {
	background:#eee;
}
.ImageDropHover {
	background:#eee;
}
.ImageModal-frame {
	@include layout(12);
	border: solid black 2px;
}
.ImageModal-align {
	@include layout(2,both,2);
}
.ImageModal-widthl {
	@include layout(1,left,1);
	text-align: right;
}
.ImageModal-width {
	@include layout(1,right);
}

.html-min-btn{
	padding: 5px;
	color: black;
	cursor: pointer;
	
	padding-left:10px;
	padding-right:10px;
	text-decoration: none;
	border: 1px solid #aaa;
	margin-left: 0px;
	margin-right: -2px;
	margin-top:10px;
	display: inline-block;
	height: 25px;
	border-radius: 3px;
	margin-left:10px;
}
.html-min-btn:hover {
	background:#eee;
}
.html-feedback-editor {
	height:200px;
	background-color: white;
	overflow:scroll;
}
.HTMLEditUI-tool{
	@include layout(12,none);
	margin-bottom:5px;
}
.HTMLEditUI-html{
	overflow-y: scroll;
	min-width:400px;
	min-height: 200px;
	resize:both;
	background-color: white;
	padding:10px;
	border: solid 1px green;
}