.arrowBtn, .upArrowBtn, .upArrowBtn:disabled, .downArrowBtn, .downArrowBtn:disabled, .rightArrowBtn, .rightArrowBtn:disabled, .leftArrowBtn, .leftArrowBtn:disabled, .noArrowBtn {
    background: none;
    border: 0; 
}

$arrowBorderWidth: 3.5px;
$arrowBorderDouble: $arrowBorderWidth*2;
$arrowMarginTop: 10px;
$arrowMarginBottom: 3px;
$arrowMarginRight: 2px;

  .upArrowBtn, .upArrowBtn:disabled {
    width: 0;
    height: 0;
    border-left: $arrowBorderWidth solid transparent;
    border-right: $arrowBorderWidth solid transparent;
    border-bottom: $arrowBorderDouble solid $color-primary;
    margin-top: $arrowMarginTop;
    margin-bottom: $arrowMarginBottom;
    margin-right: $arrowMarginRight;
    padding: 0px;
    float: left;
    position: relative; 
  }
  
  .upArrowBtn:hover {
    border-bottom: $arrowBorderDouble solid lighten($color-primary,20%);
    cursor: pointer; 
  }
  
  .upArrowBtn:disabled {
    border-bottom-color: $color-disabled; 
  }
  
  .upArrowBtn:disabled:hover {
    border-bottom: $arrowBorderDouble solid $color-disabled;
    cursor: default; 
  }
  
  .downArrowBtn, .downArrowBtn:disabled {
    width: 0;
    height: 0;
    border-left: $arrowBorderWidth solid transparent;
    border-right: $arrowBorderWidth solid transparent;
    border-top: $arrowBorderDouble solid $color-primary;
    margin-top: $arrowMarginTop;
    margin-bottom: $arrowMarginBottom;
    margin-right: $arrowMarginRight;
    padding: 0px;
    float: left;
    position: relative;
    box-sizing: border-box; 
  }
  
  .downArrowBtn:hover {
    border-top: $arrowBorderDouble solid lighten($color-primary,20%);
    cursor: pointer; 
  }
  
  .downArrowBtn:disabled {
    border-top-color: $color-disabled; 
  }
  
  .downArrowBtn:disabled:hover {
    border-top: $arrowBorderDouble solid $color-disabled;
    cursor: default; 
  }
