  .rightArrowBtn, .rightArrowBtn:disabled {
    width: 0;
    height: 0;
    border-top: $arrowBorderWidth solid transparent;
    border-bottom: $arrowBorderWidth solid transparent;
    border-left: $arrowBorderDouble solid $color-primary;
    margin-top: $arrowMarginTop;
    margin-bottom: $arrowMarginBottom;
    margin-right: $arrowMarginRight;
    box-sizing: border-box;
    float: left;
    position: relative; 
  }
  
  .rightArrowBtn:hover {
    border-left: $arrowBorderDouble solid lighten($color-primary,20%);
    cursor: pointer; 
  }
  
  .rightArrowBtn:disabled {
    border-left-color: $color-disabled; 
  }
  
  .rightArrowBtn:disabled:hover {
    border-left: $arrowBorderDouble solid $color-disabled;
    cursor: default; 
  }
  .leftArrowBtn, .leftArrowBtn:disabled {
    width: 0;
    height: 0;
    border-top: $arrowBorderWidth solid transparent;
    border-bottom: $arrowBorderWidth solid transparent;
    border-right: $arrowBorderDouble solid $color-primary;
    margin-top: $arrowMarginTop;
    margin-bottom: $arrowMarginBottom;
    margin-right: $arrowMarginRight;
    box-sizing: border-box;
    float: left;
    position: relative; 
  }
  
  .leftArrowBtn:hover {
    border-right: $arrowBorderDouble solid lighten($color-primary,20%);
    cursor: pointer; 
  }
  
  .leftArrowBtn:disabled {
    border-right-color: $color-disabled; }
  
  .leftArrowBtn:disabled:hover {
    border-right: $arrowBorderDouble solid $color-disabled;
    cursor: default; 
  }
  
  .hideArrow {
    border-color: white; 
  }
  
  .hideArrow:hover {
    border-color: white; 
  }
  
  .noArrowBtn {
    width: 0;
    height: 0;
    border-top: $arrowBorderWidth solid transparent;
    border-bottom: $arrowBorderWidth solid transparent;
    border-right: $arrowBorderDouble solid transparent;
    margin-top: $arrowMarginTop;
    margin-bottom: $arrowMarginBottom;
    margin-right: $arrowMarginRight;
    box-sizing: border-box; 
  }

  .arrowBox {
        width:$arrowBorderWidth+1;
        float:left;
        position:relative;
  }