$grid-columns:12;
$grid-column-width: .0833;


@mixin layout($columns:0,$gutters:both, $offset:0){
        // possible values for $gutters are none,left,right and both
    float:left;
    display: block;
    box-sizing: border-box;
    position: relative;
    min-height: 1px;
    @if $columns == 0 {
        @if $gutters == none {
            margin-left: percentage($offset * $grid-column-width);
            margin-right: 0px;
        } @else if $gutters == left {
            margin-left: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
            margin-right: 0px;
        } @else if $gutters == right {
            margin-left: percentage($offset * $grid-column-width);
            margin-right: $grid-gutter-width/2;
        } @else { //gutters both
            margin-left: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
            margin-right: $grid-gutter-width/2;
        }
    } @else {
        @if $gutters == none {
            width: percentage($columns * $grid-column-width);
            margin-left: percentage($offset * $grid-column-width);
            margin-right: 0px;
        } @else if $gutters == left {
            width: percentage($columns * $grid-column-width)-$grid-gutter-width/2;
            margin-left: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
            margin-right: 0px;
        } @else if $gutters == right {
            width: percentage($columns * $grid-column-width)-$grid-gutter-width/2;
            margin-left: percentage($offset * $grid-column-width);
            margin-right: $grid-gutter-width/2;
        } @else { //gutters both
            width: percentage($columns * $grid-column-width)-$grid-gutter-width;
            margin-left: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
            margin-right: $grid-gutter-width/2;
        }
    }
    
}
@mixin layoutRight($columns:0,$gutters:both, $offset:0){
    // if $columns == 0 then specify no width. Use the natural width
    // possible values for $gutters are none,left,right and both
    float: right;
    display: block;
    box-sizing: border-box;
    position: relative;
    min-height: 1px;
    @if $columns == 0 {
        @if $gutters == none {
            
            margin-right: percentage($offset * $grid-column-width);
            margin-left: 0px;
        } @else if $gutters == left {
            
            margin-left:  $grid-gutter-width/2;
            margin-right: percentage($offset * $grid-column-width);
        } @else if $gutters == right {
            
            margin-left: 0;
            margin-right: percentage($offset * $grid-column-width)+$grid-gutter-width/2;
        } @else { //gutters both
            
            margin-left: $grid-gutter-width/2;
            margin-right: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
        }
    } @else {
        @if $gutters == none {
            width: percentage($columns * $grid-column-width);
            margin-right: percentage($offset * $grid-column-width);
            margin-left: 0px;
        } @else if $gutters == left {
            width: percentage($columns * $grid-column-width)-$grid-gutter-width/2;
            margin-left:  $grid-gutter-width/2;
            margin-right: percentage($offset * $grid-column-width);
        } @else if $gutters == right {
            width: percentage($columns * $grid-column-width)-$grid-gutter-width/2;
            margin-left: 0;
            margin-right: percentage($offset * $grid-column-width)+$grid-gutter-width/2;
        } @else { //gutters both
            width: percentage($columns * $grid-column-width)-$grid-gutter-width;
            margin-left: $grid-gutter-width/2;
            margin-right: percentage($offset * $grid-column-width) + $grid-gutter-width/2;
        }
    }      
}
@mixin layoutLeft($columns,$gutters:both, $offset:0){
    // possible values for $gutters are none,left,right and both
    @include layout($columns,$gutters,$offset);
}
@mixin overlay($z:2){
    position: absolute;
    top: 0;
    left: 0;
    z-index: $z;
}
